<template>
	<el-card class="box-card">
		<div slot="header" class="clearfix">
   			<span >产品列表</span>
   			<span style="float:right">首页>产品展示</span>
  		</div>
		<div class="product-content">
			<ul>
				<li v-for="(item,key,index) in 8" :class="{marginRight0:(key+1)%4==0}">
					<el-card :body-style="{ padding: '5px' }">
						<img src="../../images/hamburger.png"  class="image">
						<div >
							
							<span>好吃的汉堡</span>
							<div class="bottom clearfix">
							  <time class="time">2017-3-10</time>
							  <router-link to="/foo">Go to Foo</router-link>
							</div>
						</div>
				    </el-card>
				</li>
			</ul>
		
		</div>
	</el-card>
</template>
<style>
.product-content{overflow: hidden;}
.product-content li{float:left;margin-right:36px;margin-bottom: 30px;}
.product-content li img{vertical-align: top;width:220px}
.product-content li.marginRight0{margin-right: 0;}
	
</style>

<script>

export default{
	data:function (){
		return {message:"product"}
	}
}
	
</script>


